.operation {
  padding: 50px 0;
  &__inner {
    background: #F1F1F1;
    border-radius: 30px;
    padding: 30px;
    position: relative;
  }
  &__img {
    position: absolute;
    left: 0;
    bottom: 100px;
    width: 100%;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__content {
    position: relative;
    z-index: 2;
  }
  &__group {
    display: flex;
    justify-content: space-between;
    &:not(:first-child) {
      margin-top: 100px;
    }
  }
  &__title {
    max-width: 500px;
    margin-right: 50px;
  }
  &__items {
    max-width: 610px;
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__item {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 34px;
      height: 34px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      border-radius: 50%;
      img {
        width: 20px;
      }
    }
    &-text {
      font-size: 15px;
      line-height: 140%;
      font-weight: 300;
      color: #000000;
      margin-left: 15px;
    }
  }
}
.stages {
  padding: 50px 0;
  &__inner {
    background: #F1F1F1;
    border-radius: 30px;
    padding: 50px;
    position: relative;
  }
  &__img {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 700px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 2;
  }
  &__left {
    max-width: 500px;
    width: 100%;
    margin-right: 50px;
  }
  &__right {
    max-width: 600px;
    width: 100%;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__item {
    display: flex;
    align-items: center;
    background: #ffffff;
    border-radius: 20px;
    padding: 30px;
    &-number {
      flex-shrink: 0;
      width: 38px;
      height: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #009834;
      border-radius: 10px;
      font-size: 24px;
      line-height: 105%;
      color: #ffffff;
    }
    &-text {
      font-size: 24px;
      line-height: 140%;
      color: #000000;
      margin-left: 20px;
    }
  }
}
.disease-info {
  background: transparent;
  padding: 50px 0;
  margin: 0;
  .info__wrap {
    margin-top: 0;
  }
}
.disease-info2 {
  &:after {
    display: none;
  }
  &:before {
    display: none;
  }
}
.rehabilitation {
  padding: 50px 0;
  &__inner {
    background: #F1F1F1;
    border-radius: 30px;
    padding: 50px;
    position: relative;
  }
  &__img {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 760px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 2;
  }
  &__left {
    width: calc(100% - 700px);
  }
  &__right {
    width: 600px;
    margin-left: 100px;
  }
  &__text {
    margin-top: 20px;
    p {
      font-size: 15px;
      line-height: 140%;
      font-weight: 300;
      color: #000000;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__right-wrap {
    background: #ffffff;
    border-radius: 10px;
    padding: 20px;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 140%;
    font-weight: 300;
    color: #009834;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 15px;
  }
  .operation__item-icon {
    background: #F7F7F7;
  }
}
.articles {
  padding: 50px 0;
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  &__swiper {
    overflow: unset;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-next,
    &-next + .swiper-slide,
    &-next + .swiper-slide + .swiper-slide{
      opacity: 1;
      pointer-events: all;
    }
  }
  .discount-card {
    border: 1px solid #F1F1F1;
    padding: 0;
    &__img {
      img {
        border-radius: 10px 10px 0 0;
      }
    }
    &__desc {
      padding: 15px;
      margin-top: 0;
    }
    &__title {
      font-size: 18px;
    }
  }
  .swiper-button-prev {
    left: -70px;
  }
  .swiper-button-next {
    right: -70px;
  }
  .swiper-pagination {
    bottom: -30px;
    display: none;
  }
}

@media screen and (max-width: 1800px) {
  .articles {
    &__slider {
      padding: 0 40px;
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
  }
}

@media screen and (max-width: 1400px) {
  .articles {
    .swiper-slide {
      &-next + .swiper-slide + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .stages {
    &__left {
      max-width: 400px;
    }
  }
  .rehabilitation {
    &__left {
     width: calc(100% - 550px);
    }
    &__right {
      width: 500px;
      margin-left: 50px;
    }
  }
}

@media screen and (max-width: 992px) {
  .operation {
    padding: 25px 0;
    &__inner {
      padding: 50px 30px;
    }
    &__group {
      display: block;
      &:not(:first-child) {
        margin-top: 50px;
      }
    }
    &__title {
      max-width: 100%;
      margin: 0 0 20px 0;
    }
    &__items {
      max-width: 100%;
    }
  }
  .stages {
    padding: 25px 0;
    &__inner {
      padding: 50px 30px;
    }
    &__img {
      width: 500px;
    }
    &__content {
      display: block;
    }
    &__left {
      max-width: 100%;
      margin: 0 0 20px 0;
    }
    &__right {
      max-width: 100%;
    }
    &__item {
      padding: 15px;
      &-text {
        font-size: 20px;
      }
    }
  }
  .disease-info {
    padding: 25px 0 0;
  }
  .rehabilitation {
    padding: 25px 0;
    &__inner {
      padding: 50px 30px;
    }
    &__img {
      width: 500px;
    }
    &__content {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 20px 0 0 0;
    }
    &__subtitle {
      font-size: 20px;
    }
  }
  .articles {
    padding: 25px 0;
    &__slider {
      margin-top: 20px;
    }
    .swiper-slide {
      &-next + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
    .discount-card {
      &__title {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .articles {
    padding-bottom: 50px;
    &__slider {
      padding: 0;
    }
    .swiper-button-prev {
      display: none;
    }
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      display: block;
    }
  }
}

@media screen and (max-width: 576px) {
  .operation {
    .container {
      padding: 0;
    }
    &__inner {
      padding: 30px 15px;
    }
  }
  .stages {
    .container {
      padding: 0;
    }
    &__inner {
      padding: 30px 15px;
    }
    &__img {
      width: 300px;
    }
    &__item {
      &-number {
        font-size: 20px;
      }
      &-text {
        font-size: 18px;
        margin-left: 10px;
      }
    }
  }
  .rehabilitation {
    .container {
      padding: 0;
    }
    &__inner {
      padding: 30px 15px;
    }
    &__img {
      width: 300px;
    }
    &__right-wrap {
      padding: 20px 15px;
    }
  }
}