.service-main {
  background: linear-gradient(180deg, #B4BCBF 0%, #C1C6C9 8.44%, #D4D9DC 91.61%, #D3D8DB 100%);;
  border-radius: 30px 30px 0 0;
  padding: 85px 0;
  position: relative;
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 700px;
    &2 {
      width: 900px;
    }
    img {
      width: 100%;
      border-radius: 0 0 30px 0;
    }
  }
  &__desc {
    max-width: 1020px;
    background: rgba(255, 255, 255, 0.70);
    border-radius: 30px;
    padding: 50px;
  }
  &__title {
    font-size: 50px;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: -1.75px;
    text-transform: uppercase;
    color: #333333;
  }
  &__text {
    font-size: 18px;
    line-height: 140%;
    font-weight: 300;
    color: #333333;
    margin-top: 10px;
  }
  &__links {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 40px;
  }
  &__link {
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFF;
    border-radius: 10px;
    padding: 3px 16px;
    margin-bottom: 10px;
    &:not(:last-child) {
      margin-right: 10px;
    }
    &:hover {
      background: #009834;
      span {
        color: #ffffff;
      }
    }
    span {
      font-size: 18px;
      line-height: 120%;
      color: #333333;
    }
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 30px;
  }
  &__btn {
    width: 233px;
    height: 54px;
    margin-right: 24px;
    margin-bottom: 15px;
    span {
      font-size: 24px;
    }
  }
  &__btn2 {
    width: 170px;
    height: 54px;
    margin-bottom: 15px;
    span {
      font-size: 24px;
    }
  }
  &__price {
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    color: #333333;
    margin-top: 40px;
    span {
      font-size: 24px;
      font-weight: 700;
    }
  }
}
.directions {
  padding: 50px 0;
  &__inner {
    background: #F1F1F1;
    border-radius: 30px;
    padding: 50px;
    position: relative;
  }
  &__img {
    position: absolute;
    left: 0;
    bottom: 0;
    max-width: 700px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 2;
  }
  &__left {
    width: 535px;
    margin-right: 50px;
  }
  &__right {
    width: calc(100% - 585px);
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 20px;
  }
}
.direction-card {
  display: flex;
  flex-direction: column;
  text-align: center;
  background: #FFF;
  border: 1px solid transparent;
  border-radius: 10px;
  padding: 20px 25px;
  &:hover {
    border: 1px solid rgba(0, 152, 52, 0.20);
    box-shadow: 0px 2px 11px 0px rgba(27, 175, 78, 0.20);
    .direction-card__title {
      color: #009834;
    }
    .direction-card__icon {
      transform: rotate(-90deg);
      svg {
        width: 20px;
        path {
          fill: #009834;
        }
      }
    }
  }
  &__title {
    font-size: 18px;
    line-height: 120%;
    font-weight: 300;
    color: #333333;
    margin-bottom: auto;
    transition: all 0.3s;
  }
  &__icon {
    width: 34px;
    height: 34px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F7F7F7;
    border-radius: 50%;
    margin: 15px auto 0;
    transition: all 0.3s;
    svg {
      width: 12px;
      transition: all 0.3s;
      path {
        transition: all 0.3s;
      }
    }
  }
}
.doctors {
  padding: 50px 0;
  &__inner {
    background: #F1F1F1;
    border-radius: 30px;
    padding: 50px;
  }
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  &__swiper {
    overflow: unset;
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(3,calc((100% / 3) - (20px * 2 / 3)));
      gap: 20px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next,
    .swiper-pagination {
      display: none;
    }
  }
}
.doctor-slide {
  background: #FFF;
  border: 1px solid transparent;
  border-radius: 20px;
  padding: 20px;
  &:hover {
    border: 1px solid rgba(0, 152, 52, 0.20);
    box-shadow: 0px 2px 11px 0px rgba(27, 175, 78, 0.20);
    .doctor-slide__name {
      color: #009834;
    }
  }
  &__wrap {
    display: flex;
    align-items: stretch;
  }
  &__img {
    width: 260px;
    margin-right: 20px;
    img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
  }
  &__desc {
    width: calc(100% - 280px);
  }
  &__post {
    font-size: 14px;
    line-height: 120%;
    font-weight: 300;
    color: #000000;
    opacity: 0.5;
    min-height: 90px;
  }
  &__name {
    font-size: 18px;
    line-height: 120%;
    font-weight: 300;
    color: #000000;
    margin-top: 20px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 20px;
  }
  &__item {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 17px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 15px;
      line-height: 120%;
      font-weight: 300;
      color: #999;
      margin-left: 5px;
      a {
        font-weight: 400;
        color: #000000;
      }
      span {
        font-weight: 400;
        color: #000000;
      }
    }
  }
  &__btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
  }
  &__btn {
    width: 260px;
    height: 50px;
    margin-right: 10px;
  }
  &__btn2 {
    width: calc(100% - 270px);
    height: 50px;
    span {
      font-size: 15px;
    }
  }
}
