.discount-card {
  background: #FFF;
  border: 1px solid transparent;
  border-radius: 10px;
  padding: 20px 20px 10px 20px;
  &:hover {
    border: 1px solid rgba(0, 152, 52, 0.20);
    box-shadow: 0px 2px 11px 0px rgba(27, 175, 78, 0.20);
    .discount-card__title {
      color: #009834;
    }
    .discount-card__btn {
      transform: rotate(-90deg);
      svg {
        width: 22px;
        path {
          fill: #009834;
        }
      }
    }
  }
  &__top {
    position: relative;
  }
  &__img {
    position: relative;
    padding-bottom: 80%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
  }
  &__status {
    position: absolute;
    top: 0;
    left: 0;
    width: 38px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #009834;
    border-radius: 10px;
    img {
      max-width: 22px;
    }
  }
  &__section {
    position: absolute;
    top: 10px;
    left: 10px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.90);
    border-radius: 10px;
    padding: 10px 15px;
    margin-right: 10px;
    span {
      font-size: 14px;
      line-height: 120%;
      font-weight: 300;
      color: #333333;
    }
  }
  &__desc {
    margin-top: 10px;
  }
  &__title {
    min-height: 40px;
    font-size: 15px;
    line-height: 120%;
    font-weight: 300;
    color: #000000;
    transition: all 0.3s;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 15px;
  }
  &__price {
    display: flex;
    align-items: center;
    margin-right: 15px;
    margin-bottom: 10px;
    span:nth-child(1) {
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      color: #000000;
    }
    span:nth-child(2) {
      font-size: 14px;
      line-height: 120%;
      font-weight: 300;
      text-decoration: line-through;
      color: #C2C2C2;
      margin-left: 10px;
    }
  }
  &__date {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 120%;
    font-weight: 300;
    color: #333333;
    margin-right: 15px;
    margin-bottom: 10px;
  }
  &__btn {
    width: 34px;
    height: 34px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F7F7F7;
    border-radius: 50%;
    margin-bottom: 10px;
    transition: all 0.3s;
    svg {
      width: 17px;
      transition: all 0.3s;
      path {
        transition: all 0.3s;
      }
    }
  }
}
.info {
  background: #F1F1F1;
  border-radius: 50px 50px 0px 0px;
  padding: 50px 0 80px 0;
  margin: 50px 0 0 0;
  position: relative;
  &__img2 {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    img {
      width: 100%;
    }
  }
  &__title {
    position: relative;
    z-index: 1;
  }
  &__items {
    position: relative;
    z-index: 1;
    max-width: 380px;
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-left: 380px;
    margin-top: 50px;
  }
  &__item {
    background: #ffffff;
    border-radius: 10px;
    padding: 20px;
    &:hover {
      .info__item-text {
        span {
          padding-left: 49px;
          &:after {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
    }
    &-text {
      font-size: 24px;
      line-height: 120%;
      font-weight: 300;
      color: #333333;
      span {
        display: block;
        font-weight: 600;
        color: #009834;
        margin-top: 15px;
        position: relative;
        transition: all 0.3s;
        &:after {
          content: '';
          position: absolute;
          top: -3px;
          left: 0;
          width: 34px;
          height: 34px;
          display: flex;
          justify-content: center;
          align-items: center;
          background-image: url("../img/check2.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: 20px;
          background-color: #F7F7F7;
          border-radius: 50%;
          opacity: 0;
          pointer-events: none;
          transition: all 0.5s;
        }
      }
    }
  }
  &__wrap {
    position: relative;
    z-index: 1;
    max-width: 1920px;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin: 0 auto;
    margin-top: 100px;
  }
  &__form-wrap {
    width: 1100px;
    background: #009834;
    border-radius: 50px;
    padding: 50px 100px 50px 140px;
    margin-right: 20px;
  }
  &__img {
    width: calc(100% - 1120px);
  }
  &__form {
    margin-top: 50px;
    &-title {
      font-size: 42px;
      line-height: 120%;
      font-weight: 400;
      letter-spacing: -1.47px;
      text-transform: uppercase;
      color: #ffffff;
    }
    &-text {
      font-size: 15px;
      line-height: 120%;
      font-weight: 300;
      color: #ffffff;
      margin-top: 10px;
    }
    &-grid {
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
    }
    &-group {
      border: none;
      padding: 0;
      margin: 0;
      input {
        width: 100%;
        height: 60px;
        background: #ffffff;
        border: none;
        border-radius: 10px;
        padding: 5px 20px;
        font-size: 15px;
        line-height: 120%;
        font-weight: 300;
        color: rgba(51, 51, 51, 1);
        &::placeholder {
          color: rgba(51, 51, 51, 0.4);;
        }
      }
      textarea {
        width: 100%;
        height: 140px;
        background: #ffffff;
        border: none;
        border-radius: 10px;
        padding: 20px;
        font-size: 15px;
        line-height: 120%;
        font-weight: 300;
        color: rgba(51, 51, 51, 1);
        resize: none;
        &::placeholder {
          color: rgba(51, 51, 51, 0.4);;
        }
      }
    }
    &-info {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
    }
    &-processing {
      display: flex;
      align-items: center;
      position: relative;
      margin-right: 20px;
      margin-bottom: 20px;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
        &:checked ~ label {
          &:before {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
      label {
        font-size: 15px;
        line-height: 200%;
        font-weight: 300;
        color: #ffffff;
        padding-left: 40px;
        position: relative;
        a {
          position: relative;
          z-index: 3;
        }
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 30px;
          height: 30px;
          background: #ffffff;
        }
        &:before {
          content: '';
          position: absolute;
          z-index: 1;
          top: 5px;
          left: 5px;
          width: 20px;
          height: 19px;
          background-image: url("../img/check.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          opacity: 0;
          pointer-events: none;
          transition: all 0.3s;
        }
      }
    }
    &-btn {
      width: 200px;
      height: 50px;
      background: #333239;
      border: 1px solid #333239;
      border-radius: 10px;
      margin-bottom: 20px;
      span {
        font-size: 15px;
        line-height: 120%;
        font-weight: 600;
        color: #ffffff;
      }
      &:hover {
        background: transparent;
        border: 1px solid #FFF;
      }
    }
  }
  &__img {
    width: calc(100% - 1120px);
    img {
      width: 100%;
      height: 100%;
      border-radius: 50px;
    }
  }
}
