.gallery {
  padding: 50px 0;
  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    margin: 0 30px 15px 0;
  }
  &__btn {
    width: 320px;
    height: 50px;
    margin-bottom: 15px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(5, calc(20% - 80px / 5));
    gap: 20px;
    margin-top: 15px;
  }
  &-card {
    position: relative;
    &:hover {
      &:after {
        opacity: 1;
        pointer-events: all;
      }
    }
    img {
      width: 100%;
      height: 100%;
      border-radius: 30px;
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background-image: url("../img/magnifier.svg");
      background-repeat: no-repeat;
      background-position: right 20px bottom 20px;
      background-size: 50px;
      background-color: rgba(255, 255, 255, 0.20);
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
  }
}
.reviews {
  padding: 50px 0;
  &__inner {
    background: #F1F1F1;
    border-radius: 30px;
    padding: 50px;
    position: relative;
  }
  &__line {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 665px;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    position: relative;
    z-index: 2;
  }
  &__content {
    max-width: 560px;
    margin-top: 30px;
    position: relative;
    z-index: 2;
  }
}
.licenses {
  padding: 50px 0 100px;
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  &__swiper {
    overflow: unset;
  }
  &__swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-next,
    &-next + .swiper-slide,
    &-next + .swiper-slide + .swiper-slide{
      opacity: 1;
      pointer-events: all;
    }
  }
  .swiper-button-prev {
    left: -70px;
  }
  .swiper-button-next {
    right: -70px;
  }
  .swiper-pagination {
    bottom: -30px;
    display: none;
  }
}
.license-slide {
  height: auto;
  border: 1px solid #F1F1F1;
  border-radius: 30px;
  padding: 5px;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: url("../img/magnifier.svg");
    background-repeat: no-repeat;
    background-position: right 20px bottom 20px;
    background-size: 50px;
    background-color: rgba(255, 255, 255, 0.20);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    border: 1px solid rgba(0, 152, 52, 0.20);
    box-shadow: 0px 2px 11px 0px rgba(27, 175, 78, 0.20);
    &:after {
      opacity: 1;
    }
  }
  img {
    width: 100%;
    border-radius: 30px;
  }
}


