.nav {
  padding: 50px 0;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li a {
      font-size: 24px;
      line-height: 120%;
      color: #000000;
      &:hover {
        color: #009834;
      }
    }
  }
}
.wrap {
  overflow: hidden;
}
.page-top {
  background: #ffffff;
  padding: 30px 0 10px;
  &__title {
    font-size: 42px;
    line-height: 120%;
    font-weight: 400;
    letter-spacing: -1.47px;
    text-transform: uppercase;
    color: #333333;
  }
}
.breadcrumbs {
  margin-top: 20px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 120%;
    color: rgba(51, 51, 51, 0.7);
    margin-bottom: 12px;
    span {
      width: 15px;
      height: 1px;
      background: rgba(51, 51, 51, 0.7);
      margin-left: 15px;
    }
    &:not(:last-child) {
      margin-right: 15px;
    }
    &:hover {
      color: #009834;
    }
  }
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-green {
  background: #009834;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  &:hover {
    .btn-green__animation {
      width: 600px;
      height: 600px;
    }
  }
  &__text {
    position: relative;
    z-index: 1;
    line-height: 120%;
    color: #ffffff;
  }
  &__animation {
    position: absolute;
    z-index: 0;
    display: block;
    width: 0;
    height: 0;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background-color: #02a83b;
    transition: width 0.8s, height 1s;
  }
}
.btn-transparent {
  background: transparent;
  border: 1px solid #333239;
  border-radius: 10px;
  &:hover {
    background: #333239;
    span {
      color: #ffffff;
    }
  }
  span {
    font-size: 15px;
    line-height: 120%;
    font-weight: 600;
    color: #333239;
  }
}
.connection-fixed {
  position: fixed;
  z-index: 5;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  background: #ffffff;
  box-shadow: 4px 4px 18px 1px rgba(34, 60, 80, 0.2);
  border-radius: 3px 3px 0px 0px;
  &__item {
    width: 65px;
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
    &:not(:last-child) {
      border-bottom: 1px solid rgba(51, 51, 51, 0.7);;
    }
    &:hover {
      background: #009834;
      border-color: #009834;
      svg path {
        stroke: #ffffff;
      }
    }
  }
}
.display2 {
  font-size: 42px;
  line-height: 130%;
  font-weight: 400;
  letter-spacing: -1.47px;
  text-transform: uppercase;
  color: #333333;
}
.swiper-button-prev {
  z-index: 1;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F7F7F7;
  border-radius: 50%;
  &:after {
    display: none;
  }
  &:hover {
    background: #FFF;
    filter: drop-shadow(0px 2px 11px rgba(27, 175, 78, 0.20));
    svg path {
      fill: #009834;
    }
  }
  svg {
    path {
      transition: all 0.3s;
    }
  }
}
.swiper-button-next {
  z-index: 1;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F7F7F7;
  border-radius: 50%;
  &:after {
    display: none;
  }
  &:hover {
    background: #FFF;
    filter: drop-shadow(0px 2px 11px rgba(27, 175, 78, 0.20));
    svg path {
      fill: #009834;
    }
  }
  svg {
    path {
      transition: all 0.3s;
    }
  }
}
.swiper-button-disabled {
  pointer-events: all !important;
  opacity: 1 !important;
}
.swiper-pagination {
  &-bullet-active {
    background: #009834;
  }
}


@media screen and (max-width: 992px) {
  .page-top {
    &__title {
      font-size: 32px;
    }
  }
  .display2 {
    font-size: 32px;
  }
}

@media screen and (max-width: 768px) {
  .connection-fixed {
    display: none;
  }
}

@media screen and (max-width: 576px) {
  .page-top {
    &__title {
      font-size: 24px;
    }
  }
  .breadcrumbs {
    margin-top: 15px;
    &__item {
      font-size: 14px;
    }
  }
  .display2 {
    font-size: 24px;
  }
}