.doctors-info {
  background: transparent;
  padding: 50px 0;
  margin: 0;
  .info__wrap {
    margin-top: 0;
  }
  .info__img {
    order: -1;
  }
  .info__form-wrap {
    padding: 50px 160px 50px 100px;
    margin: 0 0 0 20px;
  }
}
.doctors-info2 {
  &:after {
    display: none;
  }
  &:before {
    display: none;
  }
}
.help {
  background: #F1F1F1;
  border-radius: 50px;
  margin: 50px 0;
  position: relative;
  &__line {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    position: relative;
    z-index: 2;
  }
  &__desc {
    width: calc(100% - 430px);
    padding: 50px 0;
  }
  &__img {
    width: 380px;
    display: flex;
    align-items: flex-end;
    margin-left: 50px;
    img {
      width: 100%;
    }
  }
  &__title {
    span {
      color: #009834;
    }
  }
  &__text {
    font-size: 15px;
    line-height: 140%;
    font-weight: 300;
    color: #333333;
    margin-top: 10px;
  }
  &__btn {
    width: 217px;
    height: 54px;
    margin-top: 20px;
    span {
      font-size: 24px;
    }
  }
}
.visit {
  background: #F1F1F1;
  border-radius: 50px;
  padding: 30px 0;
  margin: 50px 0;
  position: relative;
  &__line {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    position: relative;
    z-index: 2;
  }
  &__img {
    flex-shrink: 0;
    width: 280px;
    margin-right: 50px;
    img {
      width: 100%;
    }
  }
  &__desc {
    max-width: 900px;
  }
  &__text {
    font-size: 15px;
    line-height: 140%;
    font-weight: 300;
    color: #333333;
  }
  &__title {
    margin-top: 10px;
    span {
      color: #009834;
    }
  }
}


@media screen and (max-width: 1600px) {
  .doctors-info {
    .info__form-wrap {
      padding: 50px;
    }
  }
}

@media screen and (max-width: 992px) {
  .doctors-info {
    padding: 25px 0 0;
  }
  .help {
    margin: 25px 0;
    &__line {
      display: none;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      display: none;
    }
  }
  .visit {
    margin: 25px 0;
    &__inner {
      padding: 0;
    }
    &__img {
      display: none;
    }
    &__desc {
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .help {
    &__btn {
      height: 50px;
      span {
        font-size: 18px;
      }
    }
  }
}